@import "https://cdn.jsdelivr.net/npm/bulma@0.9.1/css/bulma.min.css";

.sidebar {
  padding: 0 20px;
}

.main-scroll {
  height: 94vh;
  overflow: auto;
}

.scroll {
  height: 80px;
  overflow: auto;
}

.form-sub {
  position: relative;
}

.btn-left {
  width: 80px;
  border-top-left-radius: 30px !important;
  border-bottom-left-radius: 30px !important;
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

.btn-right {
  width: 80px;
  border-top-right-radius: 30px !important;
  border-bottom-right-radius: 30px !important;
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}

.sidenav-item {
  margin: 20px;
}

.flex {
  display: flex;
  justify-content: center;
}

.checkbox {
  padding: 6px;
}

//catalog

img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
  object-fit: cover;
}

.card_btn {
  padding: 0.8rem;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 4px;
  font-weight: 400;
  display: block;
  width: 100%;
  cursor: pointer;
  border: 1px solid rgba(117, 102, 102, 0.2);
}

.card_btn:hover {
  background-color: rgba(255, 255, 255, 0.12);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
}

.cards_item {
  display: flex;
  padding: 1rem;
}

@media (min-width: 40rem) {
  .cards_item {
    width: 50%;
  }
}

@media (min-width: 56rem) {
  .cards_item {
    width: 35%;
  }
}

.card {
  background-color: white;
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.card_content {
  padding: 1rem;
}

.card_title {
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 0;
}

.card_text {
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  font-weight: 400;
}

.made_by{
  font-weight: 400;
  font-size: 13px;
  margin-top: 35px;
  text-align: center;
}

.box-shadow {
  box-shadow: 2px 2px 2px lightgray;
}

.lightgray-line {
  border: 1px solid lightgray;
  box-shadow: 2px 2px 2px lightgray;
}

.mb0 {
  margin-bottom: 0 !important;
}

.pt-header {
  padding-top: 40px;
}

.card_price {
  font-weight: bold;
  font-size: 14px;
}
